
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import imagesLoaded from 'vue-images-loaded'
import axios from '@/api/axiosApi'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import Rater from 'vux/src/components/rater/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
data () {
return {
contentHTML: '',
styles: {},
noDataWrapper: false
}
},
directives: {
TransferDom,
imagesLoaded
},
components: {
VHeader, Scroll, Rater, Tab, TabItem, Popup, Grid, GridItem, isIOS, isIphoneX
},
mounted () {
this.$refs.scroll.$el.style.height = (window.innerHeight - this.$refs.scroll.$el.offsetTop) + 'px'
},
created () {
this.isMarginTop()
this._getDetail()
},
methods: {
isMarginTop () {
if (isIphoneX()) {
this.styles.marginTop = 70 + 'px'
} else {
this.styles.marginTop = 60 + 'px'
}
},
back () {
this.$router.go(-1)
},
_getDetail () {
axios.get(this, `/v1/contentDesc/${this.$route.params.contentId}`, null, (data) => {
if (data !== null) {
this.contentHTML = data.contentHtml
this.$vux.loading.show({
text: '加载中...'
})
} else {
this.noDataWrapper = true
}

setTimeout(() => {
this.$refs.scroll.scroll.refresh()
}, 200)
})
},
getRefresh () {
this._getDetail()
},
imageLoaded(instance) {

this.$vux.loading.hide()
this.$refs.scroll.scroll.refresh()
}
}
}



